import http from "@/utils/request";

export const getAllSubmit = (data) => {
    return http.post("/submit/all", data)
}

export const getSubmit = (data) => {
    return http.post("/submit/get", data)
}

export const acceptSubmit = (data) => {
    return http.post("/submit/accept", data)
}

export const deleteSubmit = (data) => {
    return http.post("/submit/delete", data)
}